import React from "react";
import styled from 'styled-components';
import ReactPlayer from "react-player";
// yarn add react-kakao-maps-sdk

const Brandlogo = styled.div`
    width: 100%;
    height: 1200px;
    padding-top: 200px;
    background-color: #ccc;
    .brand_b{
        text-align: center;
        p{
            margin: 30px 0;
            font-size: 20px;
        }
    }
`

const Corevalue = styled.div`
    width: 100%;
    height: 700px;
    background-color: #333;
    .core_b{
        display: flex;
        justify-content: center;
        div{
            width: 300px;
            background-color: #fff;
            height: 500px;
            margin: 0 10px;
            text-align: center;
            h2{
                margin-top: 20px;
                font-size: 50px;
            }
            p{
                margin-top: 100px;
                font-size: 20px;
            }
        }
        .eco{
            margin-top: 30px;
        }
    }
`

const Brandbody = styled.div`
    width: 100%;
    background-color: aquamarine;
    div{
        width: 100%;
        height: 500px;
    }
    .eco{
        text-align: right;
    }
`

function YouTubePlayer(){
    return(
        <div className="player-wrapper" style={{position: 'relative', paddingTop: '20px', paddingLeft: '450px'}}>
            <ReactPlayer
            // url="https://www.youtube.com/embed/"
            url="https://youtu.be/yEmJA1duZTI?list=TLGGscJ_AFGlh7AxNjA2MjAyNQ"
            playing
            muted
            loop
            width="1000px"
            height="500px"
            />
        </div>
    )
}

function Brand(){
    return (
        <div className="wrap">
            <Brandlogo>
                <div className="brand_b">
                    <span>.SUJAIN BRAND STORY</span>
                    <p>Balance for your life</p>
                    <span>    </span>
                    <YouTubePlayer/>
                    <p>     .        .</p>
                    <p>    , ,        .</p>
                    <p>  .  ,  ,   ,</p>
                    <p>           .</p>
                </div>
            </Brandlogo>
            <Corevalue>
                <span>Core Value</span>
                <p>, ,         .</p>
                <div className="core_b">
                    <div>
                        <h2></h2>
                        <p>SMART</p>
                        <p>    </p>
                    </div>
                    <div className="eco">
                        <h2></h2>
                        <p>ECO</p>
                        <p>     </p>
                    </div>
                    <div>
                        <h2></h2>
                        <p>PEOPLE</p>
                        <p>     </p>
                    </div>
                </div>
            </Corevalue>
            <Brandbody>
                <div className="smart">
                    <h1>Smart Life</h1>
                    <p>
                                
                          ,    .
                    </p>
                </div>
                <div className="eco">
                    <h1>Eco Space</h1>
                    <p>
                         ,        
                                  .
                    </p>
                </div>
                <div className="people">
                    <h1>People Story</h1>
                    <p>
                         .
                                  
                             .
                    </p>
                </div>
            </Brandbody>
        </div>
    )
}

export default Brand;